<template>
<div class="mainTabsClass">
  <Tabs ref="tabs" type="card" :animated="false" @on-click="tabOnClick" @on-tab-remove="handleTabRemove" :value="mainTabsActiveName">
    <TabPane label="首页" name="indexMain"></TabPane>
    <!--<TabPane label="标签二" v-if="tab1" closable>标签二的内容</TabPane>-->
    <!--<TabPane label="标签三" v-if="tab2" closable>标签三的内容</TabPane>-->
    <TabPane v-for="tab in mainTabs" :name="tab.name" :key="tab.name" :label="tab.name" closable></TabPane>
    <Button @click="handleTabsAdd" size="small" slot="extra">增加</Button>
  </Tabs>
  <slot></slot>
</div>
</template>

<script>
  export default {
    data () {
      return {
      }
    },
    computed: {
      mainTabs: {
        get () { return this.$store.state.common.mainTabs },
        set (val) { this.$store.commit('common/updateMainTabs', val) }
      },
      mainTabsActiveName: {
        get () { return this.$store.state.common.mainTabsActiveName },
        set (val) { this.$store.commit('common/updateMainTabsActiveName', val) }
      }
    },
    methods: {
      handleTabRemove (name) {
        //this['tab' + name] = false;
        console.info("handleTabRemove:"+name)

        //删除tab
        console.info("activeKey:"+this.$data.activeKey)
        console.info("this.$refs.tabs.activeKey:"+this.$refs.tabs.activeKey);

        this.mainTabs = this.mainTabs.filter(item => item.name !== name)
        this.$router.push({ name: this.$refs.tabs.activeKey }, () => {
          this.mainTabsActiveName = this.$refs.tabs.activeKey
        })
        //debugger

      },

      handleTabsAdd () {
        //this.tabs ++;
        this.$Message.info("add methods")
      },
      tabOnClick (name) {
        this.$Message.info("onClick Tab"+name)
        // debugger
        if(name != "indexMain" || name !="undefined")
          this.$router.push({name:name})
        else
          this.$router.push("/main")
      },
      //是否有这个tab存在了
      isHaveTab(activeName)
      {
        //this.mainTabs.filter()
      }

    }
    ,created () {
        console.info(">>>created")
        //this.addMainTabsActiveName(this.$router.currentRoute.path);
        this.mainTabsActiveName = this.$router.currentRoute.name;
        console.info("this.$router.currentRoute:"+this.$router.currentRoute.path)
        //debugger
    }

  }
</script>
<style>


</style>
<style scoped>
  .mainTabsClass >>> .ivu-tabs-bar { background-color: #ffffff; padding:10px 15px 0 15px;
    box-shadow: 0px 0px 2px #888888;
    margin-bottom: 0px;}
  .mainTabsClass >>> .ivu-icon-ios-close { width:22px !important; margin-right: -6px !important;}
</style>



